import React, {Component} from "react";
import {colors} from "../common/Colors";
import Title from "../common/Title";
import styled from "styled-components";

let PageTitle = styled(Title)`
      font-size: 30px;
      color: ${colors.white};
      margin: 0
      text-shadow: 1px 2px #00000044;
      font-family: 'Lexend Exa', sans-serif;
      text-align: right;
      cursor: pointer;
      height: 60px;
      line-height: 60px;
      text-align: right;
      width: 100%;
`

let PageLayout = styled.div`
  background: ${props => props.backgroundColor};
  width: 100%;
  margin-top: ${ props => props.paddingTop ? "100vh" : "0"};
  box-shadow: 0px -1px 3px  #00000040;
  text-align: right;
`
class Page extends Component{
  constructor(props){
    super(props);
    this.state = {showContent: false}
    this.handleClick = this.handleClick.bind(this)
  }
  handleClick(){
    this.setState({
      showContent: !this.state.showContent
    })
  }
  render(){
  const style = {
    background: this.props.headerColor,
    width: "100%",
    marginTop: this.props.paddingTop ? "100vh" : "0",
    boxShadow: "0px -1px 8px  #00000080",
    width: 100
  }
  return(
    <PageLayout backgroundColor ={this.props.backgroundColor} paddingTop={this.props.paddingTop}>
    <PageTitle onClick={this.props.openAction}> {this.props.title} </PageTitle>
      <div style={{padding: this.props.isOpen ? " 0" : 0,
                    maxHeight: this.props.isOpen ?`calc( 100vh - ${60*5}px)` : "0",
                    minHeight: this.props.isOpen ?`calc( 100vh - ${60*5}px)` : "0",
                    display: "flex",
                    alignItems: "center",
                    overflow: "hidden",
                    overflowY: "auto",
                    justifyContent: "space-around",
                    flexWrap: "wrap",
                    maxWidth: "100%",
                    width:"100%",
                    transition: "all 0.45s ease",
                    ...this.props.style}}>
      {this.props.children}
      </div>
    </PageLayout>
  )
  }
}

export default Page
