import React, {Component} from "react";
import {colors} from "../common/Colors";
import styled from "styled-components"

const Div = styled.div`
background: white;
box-shadow: 3px 4px 4px #00000020;
width: 400px;
height: 400px;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
`

class SmallProject extends Component{

  constructor(props){
    super(props);
    this.state = {
      hover: false
    }
    this.toggleHover = this.toggleHover.bind(this)
  }
  toggleHover(){
    this.setState({
      hover: !this.state.hover
    })
  }
  render(){
    const style ={
      background: "white",
      boxShadow: "3px 4px 3px #00000020",
      width: "400px",
      height: "400px",
      position: "relative",
      display: "flex",
      flexDirection: "column",
      justifyContent: "flex-end",
      alignItems: "center",
      overflow: "hidden",
      background: `${colors.lightBlue}`  ,
      color: "white",
      ...this.props.style
    }
    const button = {
      padding: "8px 40px",
      background: "#00000020",
      border: `1px solid ${colors.lightGreen}`,
      color: "white",
      borderRadius: "2px",
      cursor: "pointer",
      textDecoration: "none",
      fontWeight: "bold"
    }
    const hover = {
      background: "#00000044",
      width: "100%",
      height: "100%",
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      alignItems: "center",
      transition: "0.3s ease-in-out",
      opacity: this.state.hover ? 1 : 0,
      position: "absolute"
    }
    console.log(this.props.src)
    return(
        <div onMouseEnter={this.toggleHover} onMouseLeave={this.toggleHover} style={style}>
        <div style ={{
          position: "absolute",
          height: "100%",
          width: "100%",
          backgroundImage: `url(${this.props.src})`  ,
          backgroundSize: "cover",
          backgroundPosition: "center",
          transform: this.state.hover ? "scale(1.2)" : "scale(1)",
          transition: "all 1s ease",
          opacity: this.state.hover ? "0.7" : "1",
          zIndex: 0
        }}>
        </div>
          <div style={hover}>
            <a style={button} href={this.props.href} target="_blank"> View </a>
          </div>
          <p style={{fontSize: "20px", letterSpacing: "2px", zIndex: "2"  }}> {this.props.children} </p>
        </div>
    )
  }
}

export default SmallProject
